<script>

    import products from './assets/products.json';
    import categories from './assets/categories.json';
    import FoodDetails from './components/FoodDetails.vue';


    export default {
        components: {
            'food-details': FoodDetails
        },
        data() {
            return {
                products: products,
                categories: {},
                categoriesOrdered: []
            }
        },
        created() {
            this.categoriesOrdered = categories.filter((c) => c.status).sort((a, b) => a - b);

            let categories_ = {};
            for (let categorieIndex in this.categoriesOrdered) {
                let categorie = categories[categorieIndex];
                categories_[categorie.id] = categorie;
            }

            for (let productIndex in products.products) {
                let product = products.products[productIndex];
                if (product.discount != 0) {
                    product.price_with_discount = Math.round(product.price - product.price * product.discount / 100);
                }
                let productCategoriesArray = product.category_ids.map((c) => { return c.id });
                for (let categorieIdIndex in productCategoriesArray) {
                    let categorieId = productCategoriesArray[categorieIdIndex];
                    if (!categories_[categorieId]['products']) {
                        categories_[categorieId]['products'] = [];
                    }
                    categories_[categorieId]['products'].push(product);
                }
            }

            this.categories = categories_;
        },
        methods: {
            openFoodDetails(product) {
                this.$refs.foodDetails.open(product);
            }
        }
    }
</script>



