<template>
    <div class="modal fade" id="modal-dialog1" tabindex="-1" role="dialog" :aria-labelledby="title" aria-hidden="true">
        <div class="modal-dialog modal-lg modal-dialog-centered" role="document">
            <div class="modal-content" v-if="product">
                <div class="modal-header">
                    <h5 class="modal-title">{{ product.name }}</h5>
                    <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                </div>
                <div class="modal-body content">
                    <h5>Quantity</h5>
                    <div class="numbers-row">
                        <input type="text" value="1" id="qty_1" class="qty2 form-control" name="quantity">
                    </div>
                    <h5>Size</h5>
                    <ul class="clearfix">
                        <li>
                            <label class="container_radio">
                                Medium<span>+ $3.30</span>
                                <input type="radio" value="option1" name="options_1">
                                <span class="checkmark"></span>
                            </label>
                        </li>
                        <li>
                            <label class="container_radio">
                                Large<span>+ $5.30</span>
                                <input type="radio" value="option2" name="options_1">
                                <span class="checkmark"></span>
                            </label>
                        </li>
                        <li>
                            <label class="container_radio">
                                Extra Large<span>+ $8.30</span>
                                <input type="radio" value="option3" name="options_1">
                                <span class="checkmark"></span>
                            </label>
                        </li>
                    </ul>
                    <h5>Extra Ingredients</h5>
                    <ul class="clearfix">
                        <li>
                            <label class="container_check">
                                Extra Tomato<span>+ $4.30</span>
                                <input type="checkbox">
                                <span class="checkmark"></span>
                            </label>
                        </li>
                        <li>
                            <label class="container_check">
                                Extra Peppers<span>+ $2.50</span>
                                <input type="checkbox">
                                <span class="checkmark"></span>
                            </label>
                        </li>
                        <li>
                            <label class="container_check">
                                Extra Ham<span>+ $4.30</span>
                                <input type="checkbox">
                                <span class="checkmark"></span>
                            </label>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

    import { Modal } from 'bootstrap'

    export default {
        data() {
            return {
                el: undefined,
                modalInstance: undefined,
                product: null
            }
        },
        mounted() {
            this.el = document.getElementById('modal-dialog1');
            this.modalInstance = new Modal(this.el);

            var app = this;

            this.el.addEventListener('hidden.bs.modal', function () {
                app.$emit('onclose', 'modal-dialog1');
            });
        },
        methods: {
            open(product) {
                this.product = product;
                this.modalInstance.show();
            },
            close() {
                this.modalInstance.hide();
            }
        }
    }
</script>
